@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700;900&display=swap');


* {
  padding: 0px;
  margin: 0px;
  font-family: 'Montserrat', sans-serif;
}

/* .wrapper {
  margin: 10px;
} */

main {
  width: 100%;
  min-height: 100vh;
  background: #DAAD98 url('https://images.unsplash.com/photo-1606055854326-12a2fdcac6c0?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2070&q=80') no-repeat;
  /* background: #DAAD98 url('https://images.unsplash.com/photo-1477346611705-65d1883cee1e?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=100') no-repeat; */
  background-position: center;
  background-size: cover;
  background-blend-mode: multiply;
  background-color: #9c9c9c;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
  font-size: 30px;
  color: rgb(0, 0, 0);

  /* word-wrap: break-word; */
}

h2 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 20px;
  color: rgb(0, 0, 0);
  padding: 10px 0px 20px 0px;
}

button {
  background-color: #DAAD86;
  border-radius: 8px;
  font-size: 16px;
  line-height: 44px;
  font-weight: 500;
  border: none;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

button:hover {
  cursor: pointer;
  background-color: #DAAD99;
}

.box {
  background-color: #fff;
  /* opacity: 0.9; */
  border-radius: 16px;
  padding: 2%;

}

@media (max-width: 500px) {
  .box {
    padding: 5%;
  }

  main {
    justify-content: start;

  }

  .box {
    margin: 120px 10px;
    width: 80%;
  }
}


.but-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.load-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.load-ring {
  width: 8%;
  color: transparent;
}

.back-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 2.5%;
}

.back-button {
  /* z-index: 1000; */
  border-radius: 18px;
  margin: 0px 10px 0px 2px;
  line-height: 0px;
  font-weight: 500;
  font-size: 30px;
}

.input-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 0px 0px 15px 0px;
}

.input-h2 {
  width: 100%;
}

input {
  line-height: 33px;
  font-size: 20px;
  font-weight: 500;
  max-width: 80%;
  border-radius: 6px;
}

.get-code-box{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  max-width: 500px;
}
